import React from "react";
import { Html } from "@react-three/drei";
import "./object-controls.scss";

export function ObjectControls({ 
  isSelected, 
  onRotate, 
  onMoveRight, 
  onMoveLeft, 
  onMoveTop, 
  onMoveBottom 
}) {
  if (!isSelected) return null;

  return (
    <Html position={[0, 2.5, 0]} center>
      <div className="object-controls">
        <button className="object-controls__button" onClick={onRotate} title="Rotate">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M12 4V1L8 5L12 9V6C15.31 6 18 8.69 18 12C18 15.31 15.31 18 12 18C8.69 18 6 15.31 6 12H4C4 16.42 7.58 20 12 20C16.42 20 20 16.42 20 12C20 7.58 16.42 4 12 4Z" fill="currentColor"/>
          </svg>
        </button>
        <button className="object-controls__button" onClick={onMoveRight} title="Move Right">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M8.59 16.59L13.17 12L8.59 7.41L10 6L16 12L10 18L8.59 16.59Z" fill="currentColor"/>
          </svg>
        </button>
        <button className="object-controls__button" onClick={onMoveLeft} title="Move Left">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M15.41 16.59L10.83 12L15.41 7.41L14 6L8 12L14 18L15.41 16.59Z" fill="currentColor"/>
          </svg>
        </button>
        <button className="object-controls__button" onClick={onMoveTop} title="Move Top">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M7.41 15.41L12 10.83L16.59 15.41L18 14L12 8L6 14L7.41 15.41Z" fill="currentColor"/>
          </svg>
        </button>
        <button className="object-controls__button" onClick={onMoveBottom} title="Move Bottom">
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M7.41 8.59L12 13.17L16.59 8.59L18 10L12 16L6 10L7.41 8.59Z" fill="currentColor"/>
          </svg>
        </button>
      </div>
    </Html>
  );
}
